.contact-us {
  max-width: 1280px;
  padding: 20px;
  margin: 0 auto;
  position: relative;

  @include onDesktop {
    text-align: left;
    padding-bottom: 180px;
  }

  @include onTablet {
    text-align: left;
    padding-bottom: 120px;
  }

  &-title {
    font-family: "Inter", sans-serif;
    font-size: 32px;
    line-height: 32px;
    text-align: center;
    letter-spacing: -1.5px;
    color: #fff;
    grid-column: 3 / 5;
    margin-bottom: 48px;

    @include onDesktop {
      font-size: 48px;
    }
    @include onTablet {
      font-size: 48px;
    }
  }

  &__info {
    margin-bottom: 24px;

    &:first-child {
      margin-top: 48px;

      @include onDesktop {
        margin-top: 0;
      }

      @include onTablet {
        margin-top: 12px;
      }
    }

    @include onDesktop {
      line-height: 150%;
    }

    &-title {
      font-family: "Poppins", sans-serif;
      font-weight: 400;
      font-size: 14px;
      color: #fff;
      margin-bottom: 8px;
    }

    &-link {
      font-family: "Poppins", sans-serif;
      font-weight: 500;
      font-size: 16px;
      color: #fff;
      text-decoration: none;
      transition: color 0.2s;

      &:hover {
        color: $header-bgc;
      }
    }

    @include onDesktop {
      position: relative;
      right: 100%;
    }
  }

  &__field {
    margin: 0 0 16px;
  }

  &__form {
    @include onDesktop {
      padding-left: 120px;
      width: 70%;
    }
    @include onTablet {
      width: 90%;
    }
  }

  &__grid {
    position: relative;
    @include onTablet {
      display: grid;
      grid-template-columns: repeat(6, 1fr);
    }

    &--info {
      @include onTablet {
        grid-column: 5 / 6;
      }
    }

    &--form {
      @include onTablet {
        grid-column: 1 / 5;
      }
    }
  }
}
